// JS

import Vue from "vue"
import App from './App.vue'

// 
import router from "./router"
// vux
import store from './store'
// mint-ui
import {Button} from 'mint-ui'
// mockServer
import './mock/mockServer'
// 
import './filters'

// 
Vue.config.productionTip = false
// (<mt-button>)
Vue.component(Button.name,Button)

new Vue({
    el: '#app',
    render: h => h(App),
    router,// vue-router
    store,// vuex
})


